import React, { Component, Fragment } from 'react';
import {
   Row, 
   Col, 
   Form, 
   Checkbox, 
   Input, 
   Dropdown, 
   message, 
   Menu, 
   Icon, 
   Radio,
   Modal,
   DatePicker,
} from 'antd';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { SelectAjax, DatePickerString } from 'components/common/index';
import { Btn, BtnClear, BtnExport } from 'components/common/button';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getBills } from 'services/bill';
import { getFeeObjects } from 'services/feeObjectService';
import { moneyToString } from 'utils/formatUtil';
import { 
  exportSummarizedRevenue,
  exportCollectingReceipts,
} from 'services/admissionReceipts';
import moment from 'moment';
import { getTrainTypes } from 'services/trainType';
import { getCourses } from 'services/course';
import { getFacultys } from 'services/faculty';

const { RangePicker } = DatePicker;

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = { 
    paramSearch: {},
    dateRangeSelected: [moment().subtract(1, 'day'),moment()],
    showModalTHTN: false,
    showModalTHKT: false,
    optionFieldDecorator: {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      receiptConfigId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Loại thu',
          },
        ],
      },
    },
  };

  componentDidMount = () => {
    const { form } = this.props;
    const paramSearch = getQueryParams();
    delete paramSearch._time;
    form.setFieldsValue(paramSearch);
    this.setState({ paramSearch });
  };
  
  onChangeDateRange = (value) => {
    console.log(value);
    this.setState({ dateRangeSelected: value });
  }

  getPaymentTime = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
        {
          id: '3',
          label: '3',
        },
        {
          id: '4',
          label: '4',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 4,
    };
    const res = {
      data,
    };
    return res;
  };

  getPaymentTypeId = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Trực tiếp',
        },
        {
          id: 'hi',
          label: 'Chuyển khoản',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 2,
    };
    const res = {
      data,
    };
    return res;
  };

  getYearEducationsCustom = async params => {
    const res = await getYearEducations(params);
    return this.updateIdToString(res);
  };

  getSemestersCustom = async params => {
    const res = await getSemesters(params);
    return this.updateIdToString(res);
  };

  updateIdToString = res => {
    const newItems = res.data.items.map(value => {
      const newValue = value;
      newValue.id = value.id.toString();
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const isCancelled = values.isCancelled || 'false';
      udpateQueryParams({ ...values, isCancelled });
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  menu = () => (
    <Menu onClick={this.handleMenuClick}>
      <Menu.Item key="THKT">
        <Icon type="printer" />
        Tổng hợp theo khoản thu
      </Menu.Item>
      <Menu.Item key="THTN">
        <Icon type="printer" />
        Tổng hợp theo ngày và người thu
      </Menu.Item>
    </Menu>
  );

  handleMenuClick = async (e) => {
    switch (e.key) {
      case "THKT": {
        this.setState({
          showModalTHKT: true,
        });
        break;
      }
      case "THTN": {
        this.setState({
          showModalTHTN: true,
        });
        break;
      }
      default:
        break;
    }
  }

  exportTHTN = (e) => { 
    e.preventDefault();
    const { dateRangeSelected } = this.state;
    this.props.form.validateFields((err, values) => {
      if(!values.trainTypeId) {
        message.warn('Vui lòng chọn Hệ');
        return;
      }
      if(!values.courseId) {
        message.warn('Vui lòng chọn Khóa');
        return;
      }
      try {
        exportCollectingReceipts({
          trainTypeId: values.trainTypeId,
          courseId: values.courseId,
          fromDate: dateRangeSelected[0].toDate(),
          toDate: dateRangeSelected[1].toDate(),
        });
        message.success('Tổng hợp theo ngày và người thu');
      } catch (error) {
        console.log('error',error);
      }
    });
  }

  exportTHKT = async () => { 
    const { dateRangeSelected } = this.state;
    await exportSummarizedRevenue(dateRangeSelected[0].toDate(),dateRangeSelected[1].toDate());
    message.success('Tổng hợp theo ngày và người thu');
  }

  hideModal = () => {
    this.setState({
      showModalTHTN: false,
      showModalTHKT: false,
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const { countReceipts, totalAmount, handleExportExcel } = this.props;
    const { isCancelled } = this.state.paramSearch;
    const { dateRangeSelected, showModalTHTN, showModalTHKT, optionFieldDecorator } = this.state;
    const yearEduId = getFieldValue('yearEduId');
    const casherId = 'fa805ff3-634a-4be5-b163-0985bdc242fc';
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    // const courseId = getFieldValue('courseId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    console.log('optionFieldDecorator',optionFieldDecorator);

    return (
      <Fragment>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row>
            <FilterPathEducate form={this.props.form} />
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Năm học">
                {getFieldDecorator('yearEduId', {})(
                  <SelectAjax
                    service={this.getYearEducationsCustom}
                    setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Học kì">
                {getFieldDecorator('semesterId', {})(
                  <SelectAjax service={this.getSemestersCustom} params={{ yearEduId }} />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Hình thức thu">
                {getFieldDecorator('paymentTypeId', {})(
                  <SelectAjax service={this.getPaymentTypeId} />
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Loại thu" required>
                {getFieldDecorator('receiptConfigId', optionFieldDecorator.receiptConfigId
                )(<SelectAjax service={getBills} typeValue="int" />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Lần thu">
                {getFieldDecorator('paymentTime', {})(<SelectAjax service={this.getPaymentTime} />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="ĐT học phí">
                {getFieldDecorator('fOId', {})(
                  <SelectAjax service={getFeeObjects} typeValue="int" />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Người lập">
                {getFieldDecorator('casherId', { initialValue: casherId })(<Input readOnly />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Số phiếu">
                {getFieldDecorator('invoiceNo', {})(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Mã SV">
                {getFieldDecorator('studentCode', {})(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6} style={{ textAlign: 'center' }}>
              <Form.Item {...formItemLayout}>
                {getFieldDecorator('isPayment', {})(
                  <Radio.Group>
                    <Radio value="false">Phiếu thu</Radio>
                    <Radio value="true">Phiếu chi</Radio>
                  </Radio.Group>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Đến ngày">
                {getFieldDecorator('fromDate', {})(<DatePickerString placeholder="Từ ngày" />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item {...formItemLayout} label="Từ ngày">
                {getFieldDecorator('toDate', {})(<DatePickerString placeholder="Đến ngày" />)}
              </Form.Item>
            </Col>
            <Col span={2} style={{ textAlign: 'center' }}>
              <Form.Item {...formItemLayout}>
                {getFieldDecorator('isCancelled', {
                  initialValue: !!isCancelled,
                  valuePropName: 'checked',
                })(<Checkbox>Phiếu hủy</Checkbox>)}
              </Form.Item>
            </Col>
            <Col span={4} style={{ textAlign: 'right' }}>
              <b style={{ marginRight: '20px' }}>Tổng số phiếu:</b>
              <b style={{ color: 'red', fontSize: '15px' }}>{countReceipts}</b>
            </Col>
            <Col span={6} style={{ textAlign: 'right' }}>
              <b style={{ marginRight: '20px' }}>Tổng số tiền:</b>
              <b style={{ color: 'red', fontSize: '15px' }}>{moneyToString(totalAmount)}</b>
            </Col>
          </Row>
          <Row style={{ margin: '10px 0px' }}>
            <Col style={{ float: 'right' }}>
              <div className="btn-group">
                <Btn htmlType="submit" title="Liệt kê phiếu" />
                <BtnClear onClick={this.handleReset} />
              </div>
              <div className="btn-group">
                <Btn title="Xuất Excel" type="primary" disabled onClick={handleExportExcel} />
              </div>
              <div className="btn-group">
                <Dropdown overlay={this.menu}>
                  <Btn>
                    In <Icon type="down" />
                  </Btn>
                </Dropdown>
              </div>
            </Col>
          </Row>
        </Form>
        <Modal
          title="Xuất tổng hợp theo khoản thu"
          visible={showModalTHKT}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={null}
        >
          <Row>
            <Col span={16}>
              <RangePicker 
                placeholder={['Từ Năm', 'Đến Năm']}
                value={dateRangeSelected}
                onChange={this.onChangeDateRange} 
                onPanelChange={this.onChangeDateRange} 
                allowClear={false}
              />
            </Col>
            <Col span={4}>
              <BtnExport title='Xuất' onClick={this.exportTHKT} />
            </Col>
          </Row>
        </Modal>
        <Modal
          title="Xuất tổng hợp theo ngày và người thu"
          visible={showModalTHTN}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={null}
        >
          <Fragment>
            <Form className="form-inline-label" layout="vertical" onSubmit={this.exportTHTN}>
              <Row>
                <Form.Item {...formItemLayout} label="Hệ">
                  {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                    <SelectAjax service={getTrainTypes} />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Khoa">
                  {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getFacultys}
                      params={{ trainTypeId }}
                      allowGetData={!!trainTypeId}
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Khoá">
                  {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getCourses}
                      params={{ trainTypeId, facultyId }}
                      allowGetData={!!facultyId}
                    />
                  )}
                </Form.Item>
              </Row>
              <Row>
                <Col span={16}>
                  <RangePicker 
                    placeholder={['Từ Năm', 'Đến Năm']}
                    value={dateRangeSelected}
                    onChange={this.onChangeDateRange} 
                    onPanelChange={this.onChangeDateRange} 
                    allowClear={false}
                  />
                </Col>
                <Col span={8}>
                  <BtnExport title='Xuất' onClick={this.exportTHTN} />
                </Col>
              </Row>
            </Form>
          </Fragment>
        </Modal>
      </Fragment>
    );
  }
}

export default SearchBar;



